  border: 2px solid transparent;
  border-radius: 0.4rem;
  width: 100%;
  background-color: #e6e6e6;
  cursor: pointer;
  text-align: left;
  color: inherit;
  transition: all 0.3s ease;
}
.select__title::after {
  display: none;
